import React, { Component } from 'react'
import AuthServices from '../services/AuthServices'
import './SignIn.scss'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import Snackbar from '@material-ui/core/Snackbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import withRouter from '../path/to/withRouter'
import Navbar from "./Users/Navbar"
import { Link } from '@material-ui/core'

const authServices = new AuthServices()
 class SignIn extends Component {
  constructor() {
    super()
    this.state = {
      UserName: '',
      UserNameFlag: false,
      Password: '',
      PasswordFlag: false,
      open: false,
      Message: '',
    }
  }
  

  handleClose = (e, reason) => {
    if (reason === 'clickaway') {
      return
    }
    this.setState({ open: false })
  }

  handleChange = (e) => {
    const { name, value } = e.target
    this.setState(
      { [name]: value },
      console.log('Name : ', name, 'Value : ', value),
    )
  }

  handleSignUp = (e) => {
    const { navigate } = this.props;
    navigate('/signup')
  }

  CheckValidation() {
    console.log('CheckValidation Calling...')

    this.setState({ UserNameFlag: false, PasswordFlag: false })

    if (this.state.UserName === '') {
      this.setState({ UserNameFlag: true })
    }
    if (this.state.Password === '') {
      this.setState({ PasswordFlag: true })
    }
  }

  handleSubmit = (e) => {
    const { navigate } = this.props;
    this.CheckValidation()
    if (this.state.UserName !== '' && this.state.Password !== '') {
      console.log('Acceptable')
      let user = {
        userName: this.state.UserName,
        password: this.state.Password,
      }
      authServices
        .SignIn(user)
        .then((data) => {
          console.log('Data : ', data)
          if (data.data.isSuccess) {
            localStorage.setItem("currentUser", JSON.stringify(user));
            localStorage.setItem("user_id", JSON.stringify(data.data.message));
            navigate('/')
            
          } else {
            console.log('Something Went Wrong')
            this.setState({ open: true, Message: 'LogIn UnSuccessfully' })
          }
        })
        .catch((error) => {
          console.log('Error : ', error)
          this.setState({ open: true, Message: 'Something Went Wrong' })
        })
    } else {
      console.log('Not Acceptable')
      this.setState({ open: true, Message: 'Please Field Mandetory Field' })
    }
  }

  render() {
    console.log('State : ', this.state)
    return (
      <>
      <div className="SignIn-Container">
        <div className="scroll-bg"></div>
        <div className="SignUp-SubContainer">
          <div className="Header">Sign In</div>
          <div className="Body">
            <form className="form">
              <TextField
                className="TextField"
                name="UserName"
                label="UserName"
                variant="outlined"
                size="small"
                error={this.state.UserNameFlag}
                value={this.state.UserName}
                onChange={this.handleChange}
              />
              <TextField
                className="TextField"
                type="password"
                name="Password"
                label="Password"
                variant="outlined"
                size="small"
                error={this.state.PasswordFlag}
                value={this.state.Password}
                onChange={this.handleChange}
              />
            </form>
            <div className='back'>
              <i class="fa-solid fa-chevron-left"></i>
              <a href="/" >Home</a>
            </div>
            <div className='forgot'>
              <a href="/" >Forgot password?</a>
            </div>
          </div>
          <div className="Buttons" style={{ alignItems: 'flex-start' }}>
            <Button className="Btn" color="primary"  onClick={this.handleSignUp}>
              Sign Up
            </Button>
            <Button
              className="Btn"
              variant="contained"
              color="primary"
              onClick={this.handleSubmit.bind(this)}
            >
              Sign In
            </Button>
          </div>
        </div>
        <Snackbar
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          open={this.state.open}
          autoHideDuration={6000}
          onClose={this.handleClose}
          message={this.state.Message}
          action={
            <React.Fragment>
              <IconButton
                size="small"
                aria-label="close"
                color="inherit"
                onClick={this.handleClose}
              >
                <CloseIcon fontSize="small" />
              </IconButton>
            </React.Fragment>
          }
        />
      </div>
      </>
    )
  }
}
export default withRouter(SignIn)