import React, { Component } from 'react';
import { variables } from '../Admin/Variables';
import "./ProfileStyle.css";

export default class UserList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            users: [],
            modalTitle: "",
            id: 0,
            fullname: "",
            address: "",
            email: "",
            phone: "",
            roleId: 0
        }
        this.state = { user: [] };
    }

    
    componentDidMount() {
        const user_id = localStorage.getItem('user_id')
        let data = JSON.parse(user_id);

      fetch(variables.API_URL + 'User/' +data)
          .then(response => response.json())
          .then(data => {
              this.setState({ user: data});
              console.log("data", data)
              
          });

  }

    changeFullName = (e) => {
        this.setState({ fullname: e.target.value });
    }
    changePhone = (e) => {
        this.setState({ phone: e.target.value });
    }
    changeEmail = (e) => {
        this.setState({ email: e.target.value });
    }
    changeAddress = (e) => {
        this.setState({ address: e.target.value });
    }

    editClick(user) {
        this.setState({
            modalTitle: "Profile",
            id: user.id,
            fullname: user.fullname, 
            address: user.address,
            email: user.email,
            phone: user.phone,
            roleId: user.roleId,
        });
    }
  
    updateClick(id) {
        fetch(variables.API_URL + 'User/' + id, {
            method: 'PUT',
            headers: {        
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                id: this.state.id,
                fullname: this.state.fullname,
                email: this.state.email,
                phone: this.state.phone,
                address: this.state.address,
                roleId: this.state.roleId
            }),
        })
        window.location.reload(false);
        console.log(this.state.id)
    }
    
    render() {
        const {
            user,
            modalTitle,
            id,
            fullname,
            address,
            email,
            phone
        } = this.state;

        return (
            
            <div>
            <div className="profile-body" >
                <h1>User Detail</h1>
                <p>You can update profile</p>
            </div>

                    <div className="profile-body">
                                <div className="input-group mb-3">
                                    <span className="input-group-text">FullName</span>
                                    <input type="text" className="form-control"
                                        value={user.fullname}
                                         />
                                </div>
                                <div className="input-group mb-3">
                                    <span className="input-group-text">Email</span>
                                    <input type="text" className="form-control"
                                        value={user.email}
                                       />
                                </div>
                                <div className="input-group mb-3">
                                    <span className="input-group-text">Address</span>
                                    <input type="text" className="form-control"
                                        value={user.address}
                                         />
                                </div>
                                <div className="input-group mb-3">
                                    <span className="input-group-text">Phone</span>
                                    <input type="text" className="form-control"
                                        value={user.phone}
                                         />
                                </div>
                                <button type="button"
                                        className="btn btn-light mr-1"
                                        data-bs-toggle="modal"
                                        data-bs-target="#exampleModal"
                                        onClick={() => this.editClick(user)}>
                                        Update
                                    </button>
                                </div>

                <div className="modal fade" id="exampleModal" tabIndex="-1" aria-hidden="true">
                    <div className="modal-dialog modal-lg modal-dialog-centered">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title">{modalTitle}</h5>
                                <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                                ></button>
                            </div>
                                
                                <div className="input-group mb-3">
                                    <span className="input-group-text">FullName</span>
                                    <input type="text" className="form-control"
                                        value={fullname}
                                        onChange={this.changeFullName} />
                                </div>
                                 
                                   
                                <div className="input-group mb-3">
                                    <span className="input-group-text">Email</span>
                                    <input type="text" className="form-control"
                                        value={email}
                                        onChange={this.changeEmail} />
                                </div>
                                 
                                   
                                <div className="input-group mb-3">
                                    <span className="input-group-text">Phone</span>
                                    <input type="text" className="form-control"
                                        value={phone}
                                        onChange={this.changePhone} />
                                </div>
                                 
                                   
                                <div className="input-group mb-3">
                                    <span className="input-group-text">Address</span>
                                    <input type="text" className="form-control"
                                        value={address}
                                        onChange={this.changeAddress} />
                                </div>
                            
                                {id !== 0 ?
                                    <button type="button"
                                        className="btn btn-primary float-start"
                                        onClick={() => this.updateClick(id)}
                                    >Update</button>
                                    : null}
                            </div>
                        </div>
                    </div>

            </div>

        )
    }
}