import { Component } from "react";
import "./NavbarAD.css";
import { Link } from "react-router-dom";
import LogoEyesight from "../../assets/img/LogoEyesight.png";



class NavbarAdmin extends Component{
    state = {clicked: false};
    handleClick = () =>{
        this.setState({ clicked: !this.state.clicked})
    }
    logout = () => {
        localStorage.removeItem('currentUser');
        localStorage.removeItem('participant');
        localStorage.removeItem('user_id');
        localStorage.removeItem('quizzid');
        localStorage.removeItem('admin_id');
        localStorage.removeItem('quizz_image');
        localStorage.removeItem('admin');
        localStorage.removeItem('imagequizz');
        localStorage.removeItem('word');
        localStorage.removeItem('context');
        localStorage.removeItem('quiz_image');

 
    };
    
    render() {

        
        const userlogin = localStorage.getItem('currentUser')
        let data = JSON.parse(userlogin);
       
        return(
            <nav className="NavbarItemsAD">
                <Link to={"/admin/dashboard"} className="navbar-logo-admin"> 
                <img src={LogoEyesight} />
                </Link>

                <div className="menu-icons" onClick={this
                .handleClick}> 
                    <i className={this.state.clicked ? "fas fa-times" : "fas fa-bars"}></i>
                    
                </div>

                <ul className={this.state.clicked ? "navbar-menu-admin active" : "navbar-menu-admin"}>
            <Link to="/admin/dashboard" className="navbar-links-admin">Home</Link>

            <Link to="/admin/userlist" className="navbar-links-admin">UserList</Link>

            <Link to="/admin/quizlist" className="navbar-links-admin">QuizList</Link>

            <Link to="/admin/questionlist" className="navbar-links-admin">QuestionList</Link>

            <Link to="/admin/signin" className="navbar-links-admin" onClick={this.logout}>Logout</Link>
  
           
                </ul>
                
            </nav>
        )
    }
}

export default NavbarAdmin;