import { Link } from "react-router-dom";
import "./HPADStyle.css";
import NavbarAdmin from "./NavbarAdmin";
import bgadmin from "../../assets/img/bg-admin.jpg";

function Homepage (){
    const userlogin = localStorage.getItem('admin')
    let admin = JSON.parse(userlogin)
    const participant = localStorage.getItem('admin_id')
    let adminID = JSON.parse(participant)
    const logout = () => {
  
        localStorage.removeItem('admin_id');
        localStorage.removeItem('admin');
        
        
    };
    return(

        adminID != null ?
        <>
        <NavbarAdmin/>
            
            
        </>
        : null
      
    )
}

export default Homepage;