import "./EyesTestStyle.css";
import React, { Component } from 'react';
import { variables } from '../Admin/Variables';
import { createAPIEndpoint, ENDPOINTS, BASE_URL } from '../../api'
import { Link } from "react-router-dom";
import useStateContext from '../../hooks/useStateContext'
export default class EyesTest extends Component {
  
    constructor(props) {
        super(props);
        this.state = {
            quizzes: [],
            quizId: 0,
            quizName: "",
            decripsion: "",
            Imagename: "",
            modalTitle: "Start",
            nickname: "",
            user_id: 0
        
        }
        console.log("Aaaaaaaaaaa",this.props)
    }

    refreshList() {
        fetch(variables.API_URL + 'Qizz')
            .then(response => response.json())
            .then(data => {
                this.setState({ quizzes: data});
            });
    }
   

    componentDidMount() {
        this.refreshList();
    }    refreshList() {
        fetch(variables.API_URL + 'Qizz')
            .then(response => response.json())
            .then(data => {
                this.setState({ quizzes: data});
            });
          
    }

    componentDidMount() {
        this.refreshList();
    }
    CreateParticipant() {
        const userid = localStorage.getItem('user_id')
        const quizzid = localStorage.getItem('quizzid')
        let data_quizzid = JSON.parse(quizzid);
        let data = JSON.parse(userid);
        let user_id = parseInt(data)
        let quizId = parseInt(data_quizzid)
 
    fetch(variables.API_URL + 'Participant', {
        method: 'POST',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({
            nickname: this.state.nickname,
            user_id : user_id,
            quizId: quizId
        })
      
    })
        .then(res => res.json())
            .then((result) => {
                localStorage.setItem("participant",result.participantId)
            }, (error) => {
                alert('Failed');
                console.log(error)
            })
        document.querySelector('.modal-backdrop').remove();
        document.body.style.overflow="visible";



}
    changeNickName = (e) => {
        this.setState({ nickname: e.target.value });
    }
    changeId = (e) => {
        this.setState({ quizId: e.target.value });
    }
    handleSignUp = (e) => {
        console.log(e.target.value);
    }

    render() {
        const {
            quizzes,
            quizId,
            quizName,
            decripsion,
            Imagename,
            modalTitle,
            nickname,
            user_id
        } = this.state;
        const userlogin = localStorage.getItem('currentUser')
        const quizz = localStorage.getItem('quizzid')
        let quizzid = JSON.parse(quizz)
        return (
            <div className="ET" >
            <h1>Eyes Test</h1>
           <p>You can test your eyesight here, choose the test you want.</p>


   {quizzes.map(quizz =>
   
    <div className="ETcard">
      <div className="et-card">
        <div className="et-image">
            <img src={BASE_URL + 'images/' + quizz.imagename} alt="image"/>
        </div>
        <h4>{quizz.quizName}</h4>
        <p>{quizz.decripsion}</p>
        {userlogin !== null ?
            <button type="button"
                    className="btn btn-primary m-2 float-end"
                    data-bs-toggle="modal"
                    data-bs-target="#exampleModal"
                    value={quizz.quizId}
                    onClick={() => { const person = quizz.quizId;
                        localStorage.setItem('quizzid',person)}}>
                        Start Test {this.person}
                    
                    
            </button>
        : null }

        {userlogin === null ?
    <Link to={'/signin'} >
  
    </Link>
         : null}         
      </div>
      </div>

    )}
       {quizzes.map(quizz =>
          <div className="modal fade" id="exampleModal" tabIndex="-1" aria-hidden="true">
                    <div className="modal-dialog modal-lg modal-dialog-centered">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title">{modalTitle}</h5>
                                <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                                ></button>
                            </div>


                            <div className="modal-body">
                                <div className="input-group mb-3">
                                    <span className="input-group-text">Nick Name</span>
                                    <input type="text" className="form-control"
                                       value={nickname}
                                      onChange={this.changeNickName}  />
                                </div>

                                <Link to={`/quizz/${quizzid}`} style={{ textDecoration: 'none', color: 'black', textAlign: "center" }} > 
                                <button type="button"
                                        className="btn btn-primary float-start"
                                        onClick={() => this.CreateParticipant()}
                                    >Start Test</button>
                                </Link>
                            </div>
                         
                        </div>
                        
                    </div>
                </div>


                )}
  </div> 
        )
    }
}


