import React, { Component } from 'react'

class Interface extends Component {

    state = {
        currentPage: 1,
        perPage: 5
    }

    handleClick = (event, i) => {
        this.setState({
            currentPage: i
        });
    }

    render() {
        const pageNumbers = [];
        for (let i = 1; i <= Math.ceil(this.props.items / this.state.perPage); i++) {
            pageNumbers.push(i);
        }

        return (
            <div>
                <ul>
                    {pageNumbers.map(number => (
                        <li key={number}
                            id={number}
                            onClick={(e) => this.handleClick(e, number)}
                        >
                            {number}
                        </li>
                    ))}
                    
                </ul>
            </div>
        );
    }
}

export default Interface;