import React, { useEffect, useState } from 'react'
import { createAPIEndpoint, ENDPOINTS, BASE_URL } from '../../api'
import useStateContext from '../../hooks/useStateContext'
import { Card, CardContent, CardMedia, CardHeader, List, ListItemButton, Typography, Box, LinearProgress } from '@mui/material'
import { getFormatedTime } from '../../helper'
import { useNavigate, useParams } from 'react-router'
import "./Quiz.css";

export default function Quiz() {
    

    const [qns, setQns] = useState([])
    const [qnIndex, setQnIndex] = useState(0)
    const [timeTaken, setTimeTaken] = useState(0)
    const { context, setContext } = useStateContext()
    const navigate = useNavigate()
    const params = useParams();

    console.log(params);

    let timer;

    const startTimer = () => {
        timer = setInterval(() => {
            setTimeTaken(prev => prev + 1)
        }, [1000])
    }

    useEffect(() => {
        setContext({
            timeTaken: 0,
            selectedOptions: []
        })
        createAPIEndpoint(ENDPOINTS.quiz)
            .fetchById(params.id) 
            .then(res => {
                setQns(res.data)
                console.log(res.data)
                startTimer()
            })
            .catch(err => { console.log(err); })

        return () => { clearInterval(timer) }
        
    }, [])


    const updateAnswer = (qnId, optionIdx) => {
        const userlogin = localStorage.getItem('participant')
        let data = JSON.parse(userlogin);
        context.participantId = data
        

      
        console.log(data)
        const temp = [...context.selectedOptions]
        temp.push({
            qnId,
            selected: optionIdx
        })
        if (qnIndex < 9) {
            setContext({ selectedOptions: [...temp] })
            setQnIndex(qnIndex + 1)
        }
        else {
            setContext({ selectedOptions: [...temp], timeTaken })
            navigate("/result")
        }
    }
    const userlogin = localStorage.getItem('user_id')
    let user_id = JSON.parse(userlogin)
    const participant = localStorage.getItem('participant')
    let participantId = JSON.parse(participant)
    const quizz = localStorage.getItem('quizzid')
    let quizzid = JSON.parse(quizz)
    const readword = localStorage.getItem('word')
    const msg = new SpeechSynthesisUtterance()
    msg.text = readword
    /* window.speechSynthesis.speak(msg) */
    console.log(readword)


  const speechHandler = (msg) => {
    msg.text = readword
    window.speechSynthesis.speak(msg)
  }
   
    
    return (
        
        
        qns.length != 0 && user_id != null && participantId != null
            ? <Card
                sx={{
                    maxWidth: 640, mx: 'auto', mt: 10,
                    '& .MuiCardHeader-action': { m: 0, alignSelf: 'center' }
                }}>
                <CardHeader
                    title={'Question ' + (qnIndex + 1) + ' of 10'}
                    action={<Typography>{getFormatedTime(timeTaken)}</Typography>} />
                <Box>
                    <LinearProgress variant="determinate" value={(qnIndex + 1) * 100 / 5} />
                </Box>
                {qns[qnIndex].imageName != null
                    ? <CardMedia
                        component="img"
                        image={BASE_URL + 'images/' + qns[qnIndex].imageName}
                        sx={{ width: 'auto', m: '10px auto' }} />
                    : null}
                <CardContent>
                    {quizzid == 11 &&
                    <Typography variant="h6">
                        {localStorage.setItem("word",qns[qnIndex].qnInWords)}
                        <button onClick={() => speechHandler(msg)}>SPEAK</button>
                       
                       
                    </Typography>
                    
                    }
                    
                    {quizzid != 11 &&
                    <Typography variant="h6">
                        {qns[qnIndex].qnInWords}
                    </Typography>
                    }
                    <List>
                        {qns[qnIndex].options.map((item, idx) =>
                            <ListItemButton disableRipple key={idx} onClick={() => updateAnswer(qns[qnIndex].qnId, idx)}>
                                <div>
                                    <b>{String.fromCharCode(65 + idx) + " . "}</b>{item}
                                </div>

                            </ListItemButton>
                        )}

                    </List>
                    
                </CardContent>
            </Card>
            : null 

                 
    )
    
}