import { Component } from "react";
import "./NavbarStyle.css";
import { Link } from "react-router-dom";
import LogoEyesight from "../../assets/img/LogoEyesight.png";



class Navbar extends Component{
    state = {clicked: false};
    handleClick = () =>{
        this.setState({ clicked: !this.state.clicked})
    }
    logout = () => {
        localStorage.removeItem('currentUser');
        localStorage.removeItem('participant');
        localStorage.removeItem('user_id');
        localStorage.removeItem('quizzid');
 
    };
    
    render() {

        
        const userlogin = localStorage.getItem('currentUser')
        let data = JSON.parse(userlogin);
       
        return(
            <nav className="NavbarItems">
                <Link to={"/"} className="navbar-logo"> 
                <img src={LogoEyesight} />
                </Link>

                <div className="menu-icons" onClick={this
                .handleClick}> 
                    <i className={this.state.clicked ? "fas fa-times" : "fas fa-bars"}></i>
                    
                </div>

                <ul className={this.state.clicked ? "navbar-menu active" : "navbar-menu"}>
            <Link to="/" className="navbar-links">Home</Link>

            <Link to="/test" className="navbar-links">Quiz</Link>

            <Link to="/chatgpt" className="navbar-links">ChatGPT</Link>

            {userlogin !== null ?
            <Link to="/history" className="navbar-links">History</Link>
            : null}

            {userlogin !== null ?
            <Link to="/signin" className="navbar-links" onClick={this.logout}>Logout</Link>
            : null}

            {userlogin !== null ?
            
            <Link to="/profile" className="navbar-links">{data.userName}</Link>
            
            : null}
             {userlogin === null ?
            <Link to="/signin" className="navbar-links">Login</Link>
            : null}
          
                </ul>
                
            </nav>
        )
    }
}

export default Navbar;