import Navbar from "../component/Users/Navbar";
import Homepage from "../component/Users/Homepage";
import About from "../component/Users/About";
import EyesTest from "../component/Users/EyesTest";
import Footer from "../component/Users/Footer";
import ChatGPT from "../component/Users/ChatGPT";

function Home (){
    
    return(
        <>
        <Navbar />
            <Homepage 
            cName="homepage"
            homepageImg="https://c4.wallpaperflare.com/wallpaper/285/172/9/sunset-forest-minimal-4k-wallpaper-preview.jpg"
            title="Your Eyes Your Light"
            text="Let's Take The Test For The Eyes"
            buttonText="Eyes Test"
            url="/test"
            btnClass="show"
            />
            <About/> 
            <EyesTest/>
            <div className="lane"  style={{backgroundColor: 'black', height: '1px'}}/>
            <ChatGPT/>
            <Footer/>
        </>
    )
}

export default Home;