import "./ChatGPTStyle.css";
import { useState } from 'react'
import React, { Component } from 'react';
import { variables } from '../Admin/Variables';
import chatgpt from "../../assets/img/chatgpt.png";
import user from "../../assets/img/user.jpg";


export default class ChatGPT extends Component {
    constructor(props) {
        super(props);
        this.state = {
            question: '',
            asnwer: "",
           
        }    
    }
    
    
    GetDataClick() {
        fetch(variables.API_URL + 'OpenAI/GetData?input=' + this.state.question ,{
            method: 'GET',
            headers: {        
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },

            
        })
        .then(res => res.json())
        .then((result) => {
            this.setState({ asnwer: result})
            //window.location.reload(false);
            
            
            console.log(result)
        }, (error) => {
            alert('Failed');
            console.log(error)
        })
        //window.location.reload(false)
        console.log(this.state.question)
    }

    changeQuestion = (e) => {
        this.setState({ question: e.target.value });
    }
    
    render() {
        const userlogin = localStorage.getItem('currentUser')

        const {
            question,
            asnwer
        } = this.state;

        return (
            
           
            <div className="chatgpt-body" >
                <h1>ChatGPT</h1>
            <p>You can send questions to Chat GPT</p>
            <div className="modal-body">
                    <div className="chat-log">
                        <div className="chat-message">
                            <div className="chat-massage-center">
                            <img className="avatar" src={user} />
                                <div className="message">
                                    Hello
                                </div>
                            </div>
                        </div>
                        <div className="chat-message chatgpt">
                            <div className="chat-massage-center">
                                <img className="chatgpt-logo" src={chatgpt} />
                                <div className="message">
                                    I'm ChatGPT
                                    <p>{asnwer}</p>
                                </div>
                            </div>
                        </div>
                    </div>

                    {userlogin === null ?
                    <div className="input-group mb-3">
                        <input className="form-control"
                        placeholder="Please login to use ChatGPT"
                        data-toggle="modal"/>
                        </div>
                    : null }

                    {userlogin !== null ?
                    <div className="input-group mb-3">
                        <input type="text" className="form-control"
                        placeholder="Please message your question..."
                        data-toggle="modal"
                        value={this.state.question}
                        onChange={this.changeQuestion} />
                        <button type="button"
                            className="btn btn-primary float-end"            
                            data-bs-target="#exampleModal"
                            onClick={() => this.GetDataClick()}>
                            Send
                        </button>
                    </div>
                    : null }
                </div>
            </div> 
            
        )
   }

}


