import React, { Component } from 'react'
import './SignUp.scss'
import AuthServices from '../services/AuthServices.js'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import withRouter from '../path/to/withRouter';
import Snackbar from '@material-ui/core/Snackbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Navbar from './Users/Navbar'

const authServices = new AuthServices()

class SignUp extends Component {
  constructor() {
    super()
    this.state = {
      UserName: '',
      Password: '',
      ConfirmPassword: '',
      FullName: '',
      Email: '',
      Phone: '',
      Address: '',

      UserNameFlag: false,
      ConfirmPasswordFlag: false,
      AddressFlag: false,

      open: false,
      Message: '',
    }
  }

  handleClose = (e, reason) => {
    if (reason === 'clickaway') {
      return
    }
    this.setState({ open: false })
  }

  handleChangeUsername = (e) => {
    this.setState({
      UserName: e.target.value
    })
  }
  handleChangePassword = (e) => {
    this.setState({
      Password: e.target.value
    })
  }
  handleChangeFullname = (e) => {
    this.setState({
      FullName: e.target.value
    })
  }
  handleChangeEmail = (e) => {
    this.setState({
      Email: e.target.value
    })
  }
  handleChangePhone = (e) => {
    this.setState({
      Phone: e.target.value
    })
  }
  handleChangeAddress = (e) => {
    this.setState({
      Address: e.target.value
    })
  }

  validateUsername = (userName) => {
    let regex = /^[a-zA-Z0-9]+$/;
    return regex.test(userName);
  }
  validatePassword = (passWord) => {
    let regex = /(?=^.{8,}$)(?=.*\d)(?=.*[a-z])(?=.*[A-Z])/;
    return regex.test(passWord);
  }
  validateFullname = (fullName) => {
    let regex = /^[a-zA-Z- ]+$/;
    return regex.test(fullName);
  }
  validateEmail = (emailAddress) => {
    let regex = /^[\w.%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,4}$/;
    return regex.test(emailAddress);
  }
  validatePhone = (phone) => {
    let regex = /^\(?([0-9]{3})\)?[-.●]?([0-9]{3})[-.●]?([0-9]{4})$/;
    return regex.test(phone);
  }
  validateAddress = (addRess) => {
    let regex = /^\(?([0-9]{3})\)?[-.●]?([0-9]{3})[-.●]?([0-9]{4})$/;
    return regex.test(addRess);
  }

  handleUsername = (e) => {
    if(!this.validateUsername(e.target.value)) {
      alert('Please Enter a Valid Username');
    }
  }
  handlePassword = (e) => {
    if(!this.validatePassword(e.target.value)) {
      alert('Password Must Have At Least 8 Characters, Including: Uppercase, Lowercase And Number');
    } 
  }
  handleFullname = (e) => {
    if(!this.validateFullname(e.target.value)) {
      alert('Full Name Must Not Have Accents');
    }
  }
  handleEmail = (e) => {
    if(!this.validateEmail(e.target.value)) {
      alert('Please Enter Valid Email Address');
    }
  }
  handlePhone = (e) => {
    if(!this.validatePhone(e.target.value)) {
      alert('Please Enter Valid Phone Number');
    } 
  }
  handleAddress = (e) => {
    if(!this.validateAddress(e.target.value)) {
      alert('Valid Address: House Number Street Name, Ward(Commune), City');
    } 
  }


  CheckValidity() {
    console.log('Check Validity Calling')
    //Reset Flag
    this.setState({
      UserNameFlag: false,
      ConfirmPasswordFlag: false,
      AddressFlag: false,
    })

    if (this.state.UserName === '') {
      this.setState({ UserNameFlag: true })
    }
    if (this.state.ConfirmPassword === '') {
      this.setState({ ConfirmPasswordFlag: true })
    }
    if (this.state.Address === '') {
      this.setState({ AddressFlag: true })
    }
  }

  handleSubmit = (e) => {
    const { navigate } = this.props;
    this.CheckValidity()
    if (
      this.state.UserName !== '' &&
      this.state.ConfirmPassword !== '' &&
      this.state.Address !== '' 
    ) {
      const data = {
        userName: this.state.UserName,
        password: this.state.Password,
        configPassword: this.state.ConfirmPassword,
        fullName: this.state.FullName,
        email: this.state.Email,
        phone: this.state.Phone,
        address: this.state.Address,
      }

      authServices
        .SignUp(data)
        .then((data) => {
          console.log('data : ', data)
          if (data.data.isSuccess) {
            navigate('/signin')
          } else {
            console.log('Sign Up Failed')
            this.setState({ open: true, Message: 'User Exist' })
          }
        })
        .catch((error) => {
          console.log('error : ', error)
          this.setState({ open: true, Message: 'Something Went Wrong' })
        })
    } else {
      console.log('Not Acceptable')
      this.setState({ open: true, Message: 'Please Fill Required Field' })
    }
  }

  handleRadioChange = (e) => {
    this.setState({ Radiovalue: e.target.value })
  }

  handleChange = (e) => {
    const { name, value } = e.target
    this.setState(
      { [name]: value },
      console.log('Name : ', name, 'Value : ', value),
    )
  }
  

  handleSignIn = (e) => {
    const { navigate } = this.props;
    e.preventDefault();
    navigate('/signin')
  }

  render() {
    console.log('state : ', this.state)
    return (
      <>
      <div className="SignUp-Container">
        <div className="SignUp-SubContainer">
          <div className="Header">Sign Up</div>
          <div className="Body">
            <form className="form">
              <TextField
                className="TextField"
                name="UserName"
                label="UserName"
                variant="outlined"
                size="small"
                onChange={this.handleChangeUsername}
                onBlur={this.handleUsername} 
                value={this.state.UserName} 
              />
              <TextField
                className="TextField"
                type="password"
                name="Password"
                label="Password"
                variant="outlined"
                size="small"
                onChange={this.handleChangePassword}
                onBlur={this.handlePassword} 
                value={this.state.Password} 
              />
              <TextField
                className="TextField"
                type="password"
                name="ConfirmPassword"
                label="Confirm Password"
                variant="outlined"
                size="small"
                error={this.state.ConfirmPasswordFlag}
                value={this.state.ConfirmPassword}
                onChange={this.handleChange}
              />
              <TextField
                className="TextField"
                name="FullName"
                label="FullName"
                variant="outlined"
                size="small"
                onChange={this.handleChangeFullname}
                onBlur={this.handleFullname} 
                value={this.state.FullName} 
              />
              <TextField
                className="TextField"
                name="Email"
                label="Email"
                variant="outlined"
                size="small"
                onChange={this.handleChangeEmail}
                onBlur={this.handleEmail} 
                value={this.state.Email} 
              />
              <TextField
                className="TextField"
                name="Phone"
                label="Phone"
                variant="outlined"
                size="small"
                onChange={this.handleChangePhone}
                onBlur={this.handlePhone} 
                value={this.state.Phone} 
              />
              <TextField
                className="TextField"
                name="Address"
                label="Address"
                variant="outlined"
                size="small"
                onChange={this.handleChangeAddress}
                onBlur={this.handleAddress} 
                value={this.state.Address}
              />

            </form>
          </div>
          <div className="Buttons">
            <Button className="Btn" color="primary" onClick={this.handleSignIn}>
              Sign In
            </Button>
            <Button
              className="Btn"
              variant="contained"
              color="primary"
              onClick={this.handleSubmit}
            >
              Sign Up
            </Button>
          </div>
        </div>
        <Snackbar
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          open={this.state.open}
          autoHideDuration={6000}
          onClose={this.handleClose}
          message={this.state.Message}
          action={
            <React.Fragment>
              <IconButton
                size="small"
                aria-label="close"
                color="inherit"
                onClick={this.handleClose}
              >
                <CloseIcon fontSize="small" />
              </IconButton>
            </React.Fragment>
          }
        />
      </div>
      </>)
  }
}
export default withRouter(SignUp)