import Covid19 from "../../assets/img/Covid19.jpg";
import HocOnline from "../../assets/img/hoc-online.jpg";
import Eyes1 from "../../assets/img/Eyes1.jpg";
import Eyes2 from "../../assets/img/Eyes2.jpg";
import Eyes3 from "../../assets/img/Eyes3.jpg";
import Eyes4 from "../../assets/img/Eyes4.jpg";
import "./AboutStyle.css";
import AboutData from "./AboutData";

const About = () =>{
    return(
        <div className="about">
            <h1>Introduce</h1>
            <p>
                Introduction to eye diseases, vision tests and solutions
            </p>

            <AboutData

            className="first-about"
            heading="The reason the website was born"
            text="The development of industrialization and modernization always goes hand in hand with technology, now most people access information through smart devices. It proves that, during the recent covid epidemic, most students and lecturers were learning - teaching through mobile devices and when we are exposed to those devices for a long time, it will affect our health, especially especially the eyes. So to solve that problem, I built vision assessment software and came up with a solution, a website Eyesight."
            img1={Covid19}
            img2={HocOnline}
            />

            <AboutData

            className="first-about-reserve"
            heading="Refractive errors and telltale signs"
            text="Refractive error is a very common eye disorder that occurs when the eye cannot clearly focus on images from the outside world. The result of refractive errors is blurred vision, sometimes impaired vision. The three most common refractive errors are: myopia, hyperopia, astigmatism. Blurred vision is the most common symptom of refractive errors. Some other common signs and symptoms of refractive errors may include: double vision, blurred vision, seeing bright lights or halos around lights, squinting, headache, eye strain , eye pain, difficulty concentrating when reading or looking at the computer."
            img1={Eyes1}
            img2={Eyes4}
            />

            <AboutData

            className="first-about"
            heading="How to take care of your eyes"
            text="When suspecting or having refractive errors, it is necessary to go to specialized facilities to be detected and measured and corrected. If you have a refractive error, you should wear glasses of the right degree, re-examine after 6 months. For children who have not yet reached adulthood, the only way to correct refractive errors is to wear glasses. For older children or adults, in addition to wearing glasses, contact lenses can also be worn. For adults over 18 years old, laser surgery can treat refractive errors when the refraction is stable. In addition, you need to know how to take care of your eyes and keep your eyes: ensure a place to study, work with adequate light, correct sitting posture, reduce all eye strain, eat enough substances (especially foods containing a lot of nutrients vitamin A)."
            img1={Eyes3}
            img2={Eyes2}
            />
        </div>
    )
}

export default About