import React, { Component } from 'react';
import { variables } from './Variables.js';
import NavbarAdmin from './NavbarAdmin.js';
import "./AdminStyle.css";

export default class UserList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            users: [],
            modalTitle: "",
            username: "",
            id: 0,
            address: "",
            email: "",
            phone: "",
            password: "",
            userIdFilter: "",
            userNameFilter: "",
            usersWithoutFilter: []
        }
    }
    FilterFn() {
        var UserIdFilter = this.state.userIdFilter;
        var UserNameFilter = this.state.userNameFilter;

        var filteredData = this.state.usersWithoutFilter.filter(
            function (user) {

                return user.username.toString().toLowerCase().includes(
                        UserNameFilter.toString().trim().toLowerCase()
                    )


            }

        );
        this.setState({ users: filteredData });
        console.log(filteredData, "aaaa", this.state.usersWithoutFilter)
    }
    sortResult(prop, asc) {
        var sortedData = this.state.usersWithoutFilter.sort(function (a, b) {
            if (asc) {
                return (a[prop] > b[prop]) ? 1 : ((a[prop] < b[prop]) ? -1 : 0);

            }
            else {
                return (b[prop] > a[prop]) ? 1 : ((b[prop] < a[prop]) ? -1 : 0);
            }
        });
        this.setState({ users: sortedData });
        console.log("sorted", sortedData, this.state.usersWithoutFilter)
    }

    changeUserIdFilter = (e) => {
        this.state.userIdFilter = e.target.value;
        this.FilterFn();
        console.log(this.state.userIdFilter)
    }
    changeUserNameFilter = (e) => {
        this.state.userNameFilter = e.target.value;
        this.FilterFn();

        console.log(this.state.userNameFilter)
    }

    refreshList() {
        fetch(variables.API_URL + 'User')
            .then(response => response.json())
            .then(data => {
                this.setState({ users: data, usersWithoutFilter: data });
            });
    }

    componentDidMount() {
        this.refreshList();
    } refreshList() {
        fetch(variables.API_URL + 'User')
            .then(response => response.json())
            .then(data => {
                this.setState({ users: data, usersWithoutFilter: data });
            });
    }

    componentDidMount() {
        this.refreshList();
    }

    changeUserName = (e) => {
        this.setState({ username: e.target.value });
    }
    changePassword = (e) => {
        this.setState({ password: e.target.value });
    }
    changeConfirmPassword = (e) => {
        this.setState({ ConfirmPassword: e.target.value });
    }
    changeFullName = (e) => {
        this.setState({ fullname: e.target.value });
    }
    changePhone = (e) => {
        this.setState({ phone: e.target.value });
    }
    changeEmail = (e) => {
        this.setState({ email: e.target.value });
    }
    changeAddress = (e) => {
        this.setState({ address: e.target.value });
    }
    changeroleId = (e) => {
        this.setState({ roleId: e.target.value });
    }
    addClick() {
        this.setState({
            modalTitle: "Add User",
            username: "",
            id: 0,
            address: "",
            email: "",
            roleId: 2,
            phone: "",
            fullname: "",
            password: '',
            ConfirmPassword: '',
        });
    }
    editClick(user) {
        this.setState({
            modalTitle: "Edit User",
            id: user.id,
            username: user.username,
            fullname: user.fullname,
            address: user.address,
            email: user.email,
            roleId: user.roleId,
            password: user.password,
            phone: user.phone

        });
    }
    createClick() {
        fetch(variables.API_URL + 'Auth/SignUp', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                username: this.state.username,
                password: this.state.password,
                configPassword: this.state.ConfirmPassword,
                fullname: this.state.fullname,
                email: this.state.email,
                phone: this.state.phone,
                address: this.state.address,
                roleId: this.state.roleId
            })

        })
            .then(res => res.json())
            .then((result) => {
                window.location.reload(false);

            }, (error) => {
                alert('Failed');
                console.log(error)
            })

    }

    updateClick(id) {
        fetch(variables.API_URL + 'User/' + id, {
            method: 'PUT',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                id: this.state.id,
                username: this.state.username,
                fullname: this.state.fullname,
                email: this.state.email,
                phone: this.state.phone,
                address: this.state.address,
                roleId: this.state.roleId,
                password: this.state.password
            }),
        })
        window.location.reload(false);

        console.log(this.state.roleId);
        console.log(this.state.username);
        console.log(this.state.id)
    }
    deleteClick(id) {
        if (window.confirm('Are you sure?')) {
            fetch(variables.API_URL + 'User/' + id, {
                method: 'DELETE',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',

                }
            })
            window.location.reload(false);

        }
    }
    render() {
        const {
            users,
            modalTitle,
            id,
            username,
            password,
            ConfirmPassword,
            fullname,
            address,
            email,
            phone
        } = this.state;
        const participant = localStorage.getItem('admin_id')
        let adminID = JSON.parse(participant)
        return (
            adminID != null ?
            <>
            <NavbarAdmin/>
            
                <div className='form-admin'>
                    <button type="button"
                        className="btn btn-primary m-2 float-end"
                        data-bs-toggle="modal"
                        data-bs-target="#exampleModal"
                        onClick={() => this.addClick()}>
                        Add User
                    </button>
                    <table className="table table-striped">
                        <thead>
                            <tr>
                                <th>
                                  
                                    Id
                                </th>
                                <th>
                                    <div className="d-flex flex-row">
                                        <input className="form-control m-2"
                                            onChange={this.changeUserNameFilter}
                                            placeholder="Filter" />

                                        <button type="button" className="btn btn-light"
                                            onClick={() => this.sortResult('username', true)}>
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-down-square-fill" viewBox="0 0 16 16">
                                                <path d="M2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2zm6.5 4.5v5.793l2.146-2.147a.5.5 0 0 1 .708.708l-3 3a.5.5 0 0 1-.708 0l-3-3a.5.5 0 1 1 .708-.708L7.5 10.293V4.5a.5.5 0 0 1 1 0z" />
                                            </svg>
                                        </button>

                                        <button type="button" className="btn btn-light"
                                            onClick={() => this.sortResult('username', false)}>
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-up-square-fill" viewBox="0 0 16 16">
                                                <path d="M2 16a2 2 0 0 1-2-2V2a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H2zm6.5-4.5V5.707l2.146 2.147a.5.5 0 0 0 .708-.708l-3-3a.5.5 0 0 0-.708 0l-3 3a.5.5 0 1 0 .708.708L7.5 5.707V11.5a.5.5 0 0 0 1 0z" />
                                            </svg>
                                        </button>
                                    </div>
                                    User Name

                                </th>
                                <th>
                                    <div className="d-flex flex-row">



                                    </div>
                                    FullName
                                </th>
                           
                                <th>
                                    <div className="d-flex flex-row">



                                    </div>
                                    Password
                                </th>
                                <th>
                                    <div className="d-flex flex-row">




                                    </div>
                                    Address
                                </th>
                                <th>
                                    <div className="d-flex flex-row">




                                    </div>
                                    Phone
                                </th>
                                <th>
                                    <div className="d-flex flex-row">




                                    </div>
                                    RoleID
                                </th>
                                <th>
                                    Options
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            {users.map(user =>
                                <tr key={user.id}>
                                    <td>{user.id}</td>
                                    <td>{user.username}</td>
                                    <td>{user.fullname}</td>
                                    <td>{user.password}</td>
                                    <td>{user.address}</td>
                                    <td>{user.phone}</td>
                                    <td>{user.roleId}</td>
                                    <td>
                                        <button type="button"
                                            className="btn btn-light mr-1"
                                            data-bs-toggle="modal"
                                            data-bs-target="#exampleModal"
                                            onClick={() => this.editClick(user)}>
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-pencil-square" viewBox="0 0 16 16">
                                                <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z" />
                                                <path fillRule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z" />
                                            </svg>
                                        </button>

                                        <button type="button"
                                            className="btn btn-light mr-1"
                                            onClick={() => this.deleteClick(user.id)}>
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-trash-fill" viewBox="0 0 16 16">
                                                <path d="M2.5 1a1 1 0 0 0-1 1v1a1 1 0 0 0 1 1H3v9a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V4h.5a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H10a1 1 0 0 0-1-1H7a1 1 0 0 0-1 1H2.5zm3 4a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 .5-.5zM8 5a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7A.5.5 0 0 1 8 5zm3 .5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 1 0z" />
                                            </svg>
                                        </button>

                                    </td>
                                </tr>
                            )}
                        </tbody>
                    </table>
                    <div className="modal fade" id="exampleModal" tabIndex="-1" aria-hidden="true">
                        <div className="modal-dialog modal-lg modal-dialog-centered">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title">{modalTitle}</h5>
                                    <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                                    ></button>
                                </div>

                                <div className="modal-body">
                                    <div className="input-group mb-3">
                                        <span className="input-group-text">User Name</span>
                                        <input type="text" className="form-control"
                                            value={username}
                                            onChange={this.changeUserName} />
                                    </div>
                                    {id === 0 ?
                                        <div className="input-group mb-3">
                                            <span className="input-group-text">Password</span>
                                            <input type="text" className="form-control"
                                                value={password}
                                                onChange={this.changePassword} />
                                        </div>
                                        : null}

                                    {id === 0 ?
                                        <div className="input-group mb-3">
                                            <span className="input-group-text">ConfirmPassword</span>
                                            <input type="text" className="form-control"
                                                value={ConfirmPassword}
                                                onChange={this.changeConfirmPassword} />
                                        </div>
                                        : null}

                                    <div className="input-group mb-3">
                                        <span className="input-group-text">FullName</span>
                                        <input type="text" className="form-control"
                                            value={fullname}
                                            onChange={this.changeFullName} />
                                    </div>
                                    <div className="input-group mb-3">
                                        <span className="input-group-text">Password</span>
                                        <input type="text" className="form-control"
                                            value={password}
                                            onChange={this.changePassword} />
                                    </div>


                                    <div className="input-group mb-3">
                                        <span className="input-group-text">Email</span>
                                        <input type="text" className="form-control"
                                            value={email}
                                            onChange={this.changeEmail} />
                                    </div>


                                    <div className="input-group mb-3">
                                        <span className="input-group-text">Phone</span>
                                        <input type="text" className="form-control"
                                            value={phone}
                                            onChange={this.changePhone} />
                                    </div>


                                    <div className="input-group mb-3">
                                        <span className="input-group-text">Address</span>
                                        <input type="text" className="form-control"
                                            value={address}
                                            onChange={this.changeAddress} />
                                    </div>



                                    <div className="input-group mb-3">
                                        <span className="input-group-text">RoleID</span>
                                        <select onChange={this.changeroleId}>
                                            <option value="1">Admin</option>
                                            <option value="2">User</option>
                                        </select>

                                    </div>                      

                                    {id === 0 ?
                                        <button type="button"
                                            className="btn btn-primary float-start"
                                            onClick={() => this.createClick()}
                                        >Create</button>
                                        : null}

                                    {id !== 0 ?
                                        <button type="button"
                                            className="btn btn-primary float-start"
                                            onClick={() => this.updateClick(id)}
                                        >Update</button>
                                        : null}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
             
                </>
                : null
        )
    }
}