import React, { Component } from 'react';
import { variables } from './Variables.js';
import axios from 'axios';
import NavbarAdmin from './NavbarAdmin.js';

export default class QuestionList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            questions: [],
            modalTitle: "",
            qnId: 0,
            quizID: 0,
            qnInWords: "",
            imageName: "",
            options: [],
            selectedFile: '',
            status: '',
            progress: 0,
            option1: "",
            option2: "",
            option3: "",
            option4: "",
            answers: 0,
            questionsIdFilter: "",
            questionsNameFilter: "",
            questionsWithoutFilter: []
        }
    }

    selectFileHandler = (event) => {
        //1. define the array for the file type e.g. png, jpeg
        const fileTypes = ['image/png', 'image/jpeg'];

        // 2. get the file type
        let file = event.target.files;
        console.log('file', file);
        let filename = event.target.files[0].name;
        localStorage.setItem("question_image", filename)
        // 3. the message for error if the file type of not matched
        let errMessage = [];
        // 4. to check the file type to match with the fileTypes array iterate 
        // through the types array
        if (fileTypes.every(extension => file[0].type != extension)) {
            errMessage.push(`The file ${file.type} extension is not supported`);
        } else {
            this.setState({
                selectedFile: file[0]
            });
        }

    };

    uploadHandler = (event) => {
        // 1. the FormData object that contains the data to be posted to the 
        // WEB API
        const formData = new FormData();
        formData.append('file', this.state.selectedFile);

        // 2. post the file to the WEB API
        axios.post("https://api-eyesight.is-tech.vn/api/UploadFile", formData, {
            onUploadProgress: progressEvent => {
                this.setState({
                    progress: (progressEvent.loaded / progressEvent.total * 100)
                })
            }
        })
            .then((response) => {
                alert("Your file is being uploaded!")
            })
            .catch((error) => {
                this.setState({ status: `upload failed ${error}` });
            })
    }

    FilterFn() {
        var questionsIdFilter = this.state.questionsIdFilter;
        var questionsNameFilter = this.state.questionsNameFilter;

        var filteredData = this.state.questionsWithoutFilter.filter(
            function (question) {

                return question.qnInWords.toString().toLowerCase().includes(
                        questionsNameFilter.toString().trim().toLowerCase()
                    )


            }

        );
        this.setState({ questions: filteredData });
        console.log(filteredData, "aaaa", this.state.questionsWithoutFilter)
    }
    sortResult(prop, asc) {
        var sortedData = this.state.questionsWithoutFilter.sort(function (a, b) {
            if (asc) {
                return (a[prop] > b[prop]) ? 1 : ((a[prop] < b[prop]) ? -1 : 0);

            }
            else {
                return (b[prop] > a[prop]) ? 1 : ((b[prop] < a[prop]) ? -1 : 0);
            }
        });
        this.setState({ questions: sortedData });
        console.log("sorted", sortedData, this.state.questionsWithoutFilter)
    }

    changequestionsIdFilter = (e) => {
        this.state.questionsIdFilter = e.target.value;
        this.FilterFn();
        console.log(this.state.questionsIdFilter)
    }
    changequestionsNameFilter = (e) => {
        this.state.questionsNameFilter = e.target.value;
        this.FilterFn();

        console.log(this.state.questionsNameFilter)
    }

    refreshList() {
        fetch(variables.API_URL + 'Question')
            .then(response => response.json())
            .then(data => {
                this.setState({ questions: data, questionsWithoutFilter: data });
                console.log("data", data)
            });
    }

    componentDidMount() {
        this.refreshList();
    } refreshList() {
        fetch(variables.API_URL + 'Question')
            .then(response => response.json())
            .then(data => {
                this.setState({ questions: data, questionsWithoutFilter: data });
                console.log("data", data)
            });
    }
    componentDidMount() {
        this.refreshList();
    }

    changeQuestionName = (e) => {
        this.setState({ qnInWords: e.target.value });
    }
    changeimageName = (e) => {
        this.setState({ imageName: e.target.value });
    }
    changeOption1 = (e) => {
        this.setState({ option1: e.target.value });
    }
    changeOption1 = (e) => {
        this.setState({ option1: e.target.value });
    }

    changeOption2 = (e) => {
        this.setState({ option2: e.target.value });
    }
    changeOption3 = (e) => {
        this.setState({ option3: e.target.value });
    }
    changeOption4 = (e) => {
        this.setState({ option4: e.target.value });
    }
    changeOption = (e, index) => {
        let options = [...this.state.options];
        options[index] = e.target.value;
        this.setState({ options });
    }
    changeAnswer = (e) => {
        this.setState({ answers: e.target.value });
    }
    changequizID = (e) => {
        this.setState({ quizID: e.target.value });
    }
    addClick() {
        this.setState({
            modalTitle: "Add Question",
            qnInWords: "",
            qnId: 0,
            imageName: "",
            options: [],
            option1: "",
            option2: "",
            option3: "",
            option4: "",
            quizID: 1,
            answers: 0,
        });
    }
    editClick(question) {
        this.setState({
            modalTitle: "Edit Question",
            qnId: question.qnId,
            qnInWords: question.qnInWords,
            imageName: question.imageName,
            options: question.options,
            option1: question.option1,
            option2: question.option2,
            option3: question.option3,
            option4: question.option4,
            answers: question.answers,
            quizID: question.quizID
        });
    }
    createClick() {
        const imagename = localStorage.getItem("question_image")
        const quizz_image = imagename.toString()
        fetch(variables.API_URL + 'Question', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                qnInWords: this.state.qnInWords,
                imageName: quizz_image,
                option1: this.state.option1,
                option2: this.state.option2,
                option3: this.state.option3,
                option4: this.state.option4,
                Answer: this.state.answers,
                quizID: this.state.quizID
            })


        })
            .then(res => res.json())
            .then((result) => {
                window.location.reload(false);

            }, (error) => {
                alert('Failed');
                console.log(error)
            })


    }

    updateClick(id) {
        const imagename = localStorage.getItem("quiz_image")
        const quizz_image = imagename.toString()
        fetch(variables.API_URL + 'Question/' + id, {
            method: 'PUT',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                qnId: this.state.qnId,
                qnInWords: this.state.qnInWords,
                imageName: quizz_image,
                option1: this.state.options[0],
                option2: this.state.options[1],
                option3: this.state.options[2],
                option4: this.state.options[3],
                Answer: this.state.answers,
                quizID: this.state.quizID,
            }),
        })
        window.location.reload(false);

        console.log(this.state.quizID);
        console.log(this.state.qnInWords);
        console.log(this.state.qnId)
    }
    deleteClick(id) {
        if (window.confirm('Are you sure?')) {
            fetch(variables.API_URL + 'Question/' + id, {
                method: 'DELETE',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',

                }
            })
            window.location.reload(false);

        }
    }
    render() {
        const {
            questions,
            modalTitle,
            qnId,
            qnInWords,
            options,
            option1,
            option2,
            option3,
            option4,
        } = this.state;

        const participant = localStorage.getItem('admin_id')
        let adminID = JSON.parse(participant)
        return (
            adminID != null ?
            <>
            <NavbarAdmin />
            
                <div className='form-admin'>
                    <button type="button"
                        className="btn btn-primary m-2 float-end"
                        data-bs-toggle="modal"
                        data-bs-target="#exampleModal"
                        onClick={() => this.addClick()}>
                        Add Question
                    </button>
                    <table className="table table-striped">
                        <thead>
                            <tr>
                                <th>
                                
                                    Id
                                </th>
                                <th>
                                    <div className="d-flex flex-row">
                                        <input className="form-control m-2"
                                            onChange={this.changequestionsNameFilter}
                                            placeholder="Filter" />

                                        <button type="button" className="btn btn-light"
                                            onClick={() => this.sortResult('qnInWords', true)}>
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-down-square-fill" viewBox="0 0 16 16">
                                                <path d="M2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2zm6.5 4.5v5.793l2.146-2.147a.5.5 0 0 1 .708.708l-3 3a.5.5 0 0 1-.708 0l-3-3a.5.5 0 1 1 .708-.708L7.5 10.293V4.5a.5.5 0 0 1 1 0z" />
                                            </svg>
                                        </button>

                                        <button type="button" className="btn btn-light"
                                            onClick={() => this.sortResult('qnInWords', false)}>
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-up-square-fill" viewBox="0 0 16 16">
                                                <path d="M2 16a2 2 0 0 1-2-2V2a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H2zm6.5-4.5V5.707l2.146 2.147a.5.5 0 0 0 .708-.708l-3-3a.5.5 0 0 0-.708 0l-3 3a.5.5 0 1 0 .708.708L7.5 5.707V11.5a.5.5 0 0 0 1 0z" />
                                            </svg>
                                        </button>
                                    </div>

                                    Question Name

                                </th>
                                <th>
                                    <div className="d-flex flex-row">



                                    </div>
                                    Image Name
                                </th>
                                <th>
                                    <div className="d-flex flex-row">




                                    </div>
                                    Option 1
                                </th>
                                <th>
                                    <div className="d-flex flex-row">




                                    </div>
                                    Option 2
                                </th>
                                <th>
                                    <div className="d-flex flex-row">




                                    </div>
                                    Option 3
                                </th>
                                <th>
                                    <div className="d-flex flex-row">




                                    </div>
                                    Option 4
                                </th>

                                <th>
                                    <div className="d-flex flex-row">




                                    </div>
                                    Answer
                                </th>
                                <th>
                                    <div className="d-flex flex-row">




                                    </div>

                                    quizID
                                </th>
                                <th>
                                    options
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            {questions.map(question =>
                                <tr key={question.qnId}>
                                    <td>{question.qnId}</td>
                                    <td>{question.qnInWords}</td>
                                    <td>{question.imageName}</td>
                                    <td>{question.options[0]}</td>
                                    <td>{question.options[1]}</td>
                                    <td>{question.options[2]}</td>
                                    <td>{question.options[3]}</td>
                                    <td>{question.answers}</td>
                                    <td>{question.quizID}</td>
                                    <td>
                                        <button type="button"
                                            className="btn btn-light mr-1"
                                            data-bs-toggle="modal"
                                            data-bs-target="#exampleModal"
                                            onClick={() => this.editClick(question)}>
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-pencil-square" viewBox="0 0 16 16">
                                                <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z" />
                                                <path fillRule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z" />
                                            </svg>
                                        </button>

                                        <button type="button"
                                            className="btn btn-light mr-1"
                                            onClick={() => this.deleteClick(question.qnId)}>
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-trash-fill" viewBox="0 0 16 16">
                                                <path d="M2.5 1a1 1 0 0 0-1 1v1a1 1 0 0 0 1 1H3v9a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V4h.5a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H10a1 1 0 0 0-1-1H7a1 1 0 0 0-1 1H2.5zm3 4a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 .5-.5zM8 5a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7A.5.5 0 0 1 8 5zm3 .5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 1 0z" />
                                            </svg>
                                        </button>

                                    </td>
                                </tr>
                            )}
                        </tbody>
                    </table>
                    <div className="modal fade" id="exampleModal" tabIndex="-1" aria-hidden="true">
                        <div className="modal-dialog modal-lg modal-dialog-centered">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title">{modalTitle}</h5>
                                    <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                                    ></button>
                                </div>

                                <div className="modal-body">
                                    <div className="input-group mb-3">
                                        <span className="input-group-text">Question Name</span>
                                        <input type="text" className="form-control"
                                            value={qnInWords}
                                            onChange={this.changeQuestionName} />
                                    </div>

                                    <div className="input-group mb-3">
                                        <span className="input-group-text">Image</span>
                                        <input type="file"
                                            onChange={this.selectFileHandler}
                                        />
                                        <button onClick={this.uploadHandler}>Upload Image</button>
                                    </div>


                                    {qnId === 0 ?
                                        <div className="input-group mb-3">
                                            <span className="input-group-text">Option 1</span>
                                            <input type="text" className="form-control"
                                                value={option1}
                                                onChange={this.changeOption1} />
                                        </div>
                                        : null}
                                    {qnId !== 0 ?
                                        <div className="input-group mb-3">
                                            <span className="input-group-text">Option 1</span>
                                            <input type="text" className="form-control"
                                                value={options[0]}
                                                onChange={e => this.changeOption(e, 0)} />
                                        </div>

                                        : null}



                                    {qnId === 0 ?
                                        <div className="input-group mb-3">
                                            <span className="input-group-text">Option 2</span>
                                            <input type="text" className="form-control"
                                                value={option2}
                                                onChange={this.changeOption2} />
                                        </div>
                                        : null}
                                    {qnId !== 0 ?
                                        <div className="input-group mb-3">
                                            <span className="input-group-text">Option 2</span>

                                            <input type="text" className="form-control"
                                                value={options[1]}
                                                onChange={e => this.changeOption(e, 1)} />
                                        </div>

                                        : null}
                                    {qnId === 0 ?
                                        <div className="input-group mb-3">
                                            <span className="input-group-text">Option 3</span>
                                            <input type="text" className="form-control"
                                                value={option3}
                                                onChange={this.changeOption3} />
                                        </div>
                                        : null}
                                    {qnId !== 0 ?
                                        <div className="input-group mb-3">
                                            <span className="input-group-text">Option 3</span>
                                            <input type="text" className="form-control"
                                                value={options[2]}
                                                onChange={e => this.changeOption(e, 2)} />
                                        </div>

                                        : null}
                                    {qnId === 0 ?
                                        <div className="input-group mb-3">
                                            <span className="input-group-text">Option 4</span>
                                            <input type="text" className="form-control"
                                                value={option4}
                                                onChange={this.changeOption4} />
                                        </div>
                                        : null}
                                    {qnId !== 0 ?
                                        <div className="input-group mb-3">
                                            <span className="input-group-text">Option 4</span>
                                            <input type="text" className="form-control"
                                                value={options[3]}
                                                onChange={e => this.changeOption(e, 3)} />
                                        </div>

                                        : null}

                                    <div className="input-group mb-3">
                                        <span className="input-group-text">Answer</span>
                                        <select onChange={this.changeAnswer}>
                                            <option>Choose the answer</option>
                                            <option value="0">Option 1</option>
                                            <option value="1">Option 2</option>
                                            <option value="2">Option 3</option>
                                            <option value="3">Option 4</option>
                                        </select>
                                    </div>



                                    <div className="input-group mb-3">
                                        <span className="input-group-text">quizID</span>
                                        <select onChange={this.changequizID}>
                                            <option value="1">Myopia Test</option>
                                            <option value="2">Light Sensitivity Test</option>
                                            <option value="3">Color Blindness Test</option>
                                            <option value="4">IQ Test</option>
                                            <option value="10">Reading Test</option>
                                            <option value="11">Listening Test</option>
                                            <option value="12">Astigmatism Test And Presbyopia Test</option>
                                        </select>
                                    </div>

                                    {qnId === 0 ?
                                        <button type="button"
                                            className="btn btn-primary float-start"
                                            onClick={() => this.createClick()}
                                        >Create</button>
                                        : null}

                                    {qnId !== 0 ?
                                        <button type="button"
                                            className="btn btn-primary float-start"
                                            onClick={() => this.updateClick(qnId)}
                                        >Update</button>
                                        : null}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                </>
                : null
            
        )
    }
}